.section {
  position: relative;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  border-radius: 7px;
  background-color: #fff;
  padding: 17px 20px;
  box-shadow: 0px 2px 7px 0 rgba(0, 0, 0, 0.25);

  &.summary {
    height: 270px;
  }

  &.emotion {
    height: 110px;
  }
}
